'use client';

import Header from '../components/header/page';
import Footer from '../components/footer/page';
import Link from 'next/link';
import { useState, useEffect } from 'react';
import { useForm, Controller } from 'react-hook-form';
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Label } from "@radix-ui/react-label";

const CartPage: React.FC = () => {
  // State to manage cart items
  const [cartItems, setCartItems] = useState<any[]>([]);

  // State to manage address form visibility
  const [showAddressForm, setShowAddressForm] = useState(false);

  // State to manage payment form visibility
  const [showPaymentForm, setShowPaymentForm] = useState(false);

  // State to manage address input
  const [address, setAddress] = useState({
    street: '',
    city: '',
    state: '',
    postalCode: '',
    country: '',
  });

  // Load cart items from localStorage on mount
  useEffect(() => {
    const items = JSON.parse(localStorage.getItem('cart') || '[]');
    setCartItems(items);
    console.log('Loaded cart items:', items); // Debug loaded items
  }, []);

  // Calculate total price
  const getTotal = () => {
    return cartItems.reduce((total: number, item: any) => total + item.price * item.quantity, 0);
  };

  // Update quantity
  const updateQuantity = (id: string, newQuantity: number) => {
    const updatedCart = cartItems.map((item: any) =>
      item.id === id ? { ...item, quantity: Math.max(1, newQuantity) } : item
    );
    setCartItems(updatedCart);
    localStorage.setItem('cart', JSON.stringify(updatedCart));
  };

  // Remove item
  const removeItem = (id: string) => {
    console.log('Attempting to remove item with id:', id); // Debug id to remove
    console.log('Current cart before removal:', cartItems); // Debug current cart
    const updatedCart = cartItems.filter((item: any) => item.id !== id); // Only removes the item with matching id
    console.log('Cart after removal:', updatedCart); // Debug cart after removal
    setCartItems(updatedCart);
    localStorage.setItem('cart', JSON.stringify(updatedCart));
  };

  // Handle address input change
  const handleAddressChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setAddress((prev) => ({ ...prev, [name]: value }));
  };

  // Handle address form submission
  const handleAddressSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    console.log('Submitted address:', address);
    setShowAddressForm(false);
    setShowPaymentForm(true); // Show payment form after address submission
  };

  // Payment form configuration
  const paymentForm = useForm({
    defaultValues: {
      paymentMethod: '',
      cardNumber: '',
      cardholderName: '',
      cvv: '',
      expiryDate: '',
    },
  });

  // Handle payment form submission
  const onPaymentSubmit = (data: any) => {
    console.log('Payment details submitted:', data);
    alert('Payment processed! Order placed successfully.');
    // Reset forms and cart (optional)
    setShowPaymentForm(false);
    localStorage.removeItem('cart');
    setCartItems([]);
  };

  return (
    <div className="min-h-screen bg-stone-50 text-stone-900 font-serif">
      <Header />
      <main className="max-w-6xl mx-auto py-10 px-4">
        <nav className="flex items-center text-sm text-gray-500 mb-4">
          <Link href="/" className="hover:underline">Home</Link> / <span>Cart</span>
        </nav>
        <h1 className="text-4xl font-bold mb-6">Your Cart</h1>
        {cartItems.length === 0 ? (
          <p className="text-gray-600">Your cart is empty. <Link href="/" className="text-teal-600 underline">Continue shopping</Link></p>
        ) : (
          <>
            <div className="space-y-6">
              {cartItems.map((item: any) => (
                <div key={item.id} className="flex items-center justify-between bg-white p-4 rounded-lg shadow-md">
                  <img
                    src={item.images[0]}
                    alt={item.title}
                    className="w-24 h-32 object-cover rounded-md"
                  />
                  <div className="flex-1 ml-4">
                    <h3 className="text-lg font-semibold">{item.title}</h3>
                    <p className="text-gray-600">Color: {item.color}</p>
                    <p className="text-gray-600">Size: {item.size || 'N/A'}</p>
                    <p className="text-teal-600 font-semibold">₹{item.price}</p>
                  </div>
                  <div className="flex items-center space-x-4">
                    <div className="flex items-center space-x-2">
                      <button
                        onClick={() => updateQuantity(item.id, item.quantity - 1)}
                        className="w-8 h-8 bg-gray-200 rounded-full flex items-center justify-center hover:bg-gray-300"
                      >
                        -
                      </button>
                      <span className="w-8 text-center">{item.quantity}</span>
                      <button
                        onClick={() => updateQuantity(item.id, item.quantity + 1)}
                        className="w-8 h-8 bg-gray-200 rounded-full flex items-center justify-center hover:bg-gray-300"
                      >
                        +
                      </button>
                    </div>
                    <button
                      onClick={() => removeItem(item.id)}
                      className="text-red-600 hover:text-red-800"
                    >
                      Remove
                    </button>
                  </div>
                </div>
              ))}
            </div>
            <div className="mt-6 p-4 bg-white rounded-lg shadow-md">
              <h2 className="text-xl font-semibold">Order Summary</h2>
              <p className="text-gray-600">Subtotal: ₹{getTotal()}</p>
              <p className="text-gray-600">Shipping: Free (over ₹100)</p>
              <p className="text-gray-600 font-semibold mt-2">Total: ₹{getTotal()}</p>
              {showAddressForm ? (
                <form onSubmit={handleAddressSubmit} className="mt-4 space-y-4">
                  <div>
                    <Label htmlFor="street">Street Address</Label>
                    <Input
                      id="street"
                      type="text"
                      name="street"
                      value={address.street}
                      onChange={handleAddressChange}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="city">City</Label>
                    <Input
                      id="city"
                      type="text"
                      name="city"
                      value={address.city}
                      onChange={handleAddressChange}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="state">State</Label>
                    <Input
                      id="state"
                      type="text"
                      name="state"
                      value={address.state}
                      onChange={handleAddressChange}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="postalCode">Postal Code</Label>
                    <Input
                      id="postalCode"
                      type="text"
                      name="postalCode"
                      value={address.postalCode}
                      onChange={handleAddressChange}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="country">Country</Label>
                    <Input
                      id="country"
                      type="text"
                      name="country"
                      value={address.country}
                      onChange={handleAddressChange}
                      required
                    />
                  </div>
                  <Button type="submit" className="w-full mt-4">
                    Submit Address
                  </Button>
                </form>
              ) : showPaymentForm ? (
                <Form {...paymentForm}>
                  <form onSubmit={paymentForm.handleSubmit(onPaymentSubmit)} className="mt-4 space-y-4">
                    <FormField
                      control={paymentForm.control}
                      name="paymentMethod"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Payment Method</FormLabel>
                          <Select onValueChange={field.onChange} defaultValue={field.value}>
                            <FormControl>
                              <SelectTrigger>
                                <SelectValue placeholder="Select payment method" />
                              </SelectTrigger>
                            </FormControl>
                            <SelectContent>
                              <SelectItem value="creditCard">Credit Card</SelectItem>
                              <SelectItem value="debitCard">Debit Card</SelectItem>
                              <SelectItem value="upi">UPI</SelectItem>
                              <SelectItem value="netBanking">Net Banking</SelectItem>
                              <SelectItem value="cod">Cash on Delivery</SelectItem>
                            </SelectContent>
                          </Select>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                    {paymentForm.watch('paymentMethod') === 'creditCard' && (
                      <>
                        <FormField
                          control={paymentForm.control}
                          name="cardNumber"
                          render={({ field }) => (
                            <FormItem>
                              <FormLabel>Card Number</FormLabel>
                              <FormControl>
                                <Input placeholder="1234 5678 9012 3456" {...field} />
                              </FormControl>
                              <FormMessage />
                            </FormItem>
                          )}
                        />
                        <FormField
                          control={paymentForm.control}
                          name="cardholderName"
                          render={({ field }) => (
                            <FormItem>
                              <FormLabel>Cardholder Name</FormLabel>
                              <FormControl>
                                <Input placeholder="John Doe" {...field} />
                              </FormControl>
                              <FormMessage />
                            </FormItem>
                          )}
                        />
                        <div className="grid grid-cols-2 gap-4">
                          <FormField
                            control={paymentForm.control}
                            name="cvv"
                            render={({ field }) => (
                              <FormItem>
                                <FormLabel>CVV</FormLabel>
                                <FormControl>
                                  <Input placeholder="123" {...field} />
                                </FormControl>
                                <FormMessage />
                              </FormItem>
                            )}
                          />
                          <FormField
                            control={paymentForm.control}
                            name="expiryDate"
                            render={({ field }) => (
                              <FormItem>
                                <FormLabel>Expiry Date</FormLabel>
                                <FormControl>
                                  <Input placeholder="MM/YY" {...field} />
                                </FormControl>
                                <FormMessage />
                              </FormItem>
                            )}
                          />
                        </div>
                      </>
                    )}
                    <Button type="submit" className="w-full mt-4">
                      Complete Payment
                    </Button>
                  </form>
                </Form>
              ) : (
                <Button
                  onClick={() => setShowAddressForm(true)}
                  className="mt-4 w-full bg-teal-600 text-white font-medium py-2 rounded hover:bg-teal-700 transition-colors"
                >
                  Proceed to Checkout
                </Button>
              )}
            </div>
          </>
        )}
      </main>
      <Footer />
    </div>
  );
};

export default CartPage;